* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PTmono", "Arial", sans-serif;
  color: #666666;
  font-weight: 400;
  font-style: normal;
  background-color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none;
  opacity: 1;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }
  a:hover, a:focus {
    opacity: 0.7; }
  a:active {
    opacity: 0.5; }
  @media (max-width: 1023px) {
    a:hover, a:focus {
      opacity: 1; }
    a:active {
      opacity: 1; } }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  display: inline-block;
  padding-top: 15px;
  padding-bottom: 13px;
  vertical-align: middle;
  font-family: "RobotoCondesed", "Arial", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer;
  z-index: 1;
  opacity: 1;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }
  .button:hover, .button:focus {
    opacity: 0.7; }
  .button:active {
    opacity: 0.5; }
  @media (max-width: 1023px) {
    .button:hover, .button:focus {
      opacity: 1; }
    .button:active {
      opacity: 1; } }

